.input {
    width:2em;
    text-align:center;
    font-size:2em;
    border:0;
    border-bottom: 3px solid #b4aee8;
    outline: none;
    color:white;
    background-color: #440a67;
}

.button {
    padding:10px;
    margin:10px;
    font-size:1em;
    border-radius:10px;
    outline:none;
    background-color: #93329e;
    color:white;
    border:1px solid #b4aee8;
}